.container {
    margin:10px auto;
    padding:10px 20px;
    width:1024px;
    height:800px;
    border:1px solid #ccc;
    border-radius: 10px;
    box-shadow: 0 0 4px 2px rgba(0,0,0,0.4);
    overflow: scroll;
}
.searchField {
    border-radius: 10px;
    font-size: 1rem;
    width:700px;
    padding: 10px 20px;
    margin:10px 20px;
    outline:none;
}
.searchButton {
    border-radius: 10px;
    font-size: 1rem;
    padding: 12px 30px;
    margin:10px 20px;
    outline:none;
    cursor:pointer;
}
.recipeCard{
    display:inline-block;
    width:300px;
    height:500px;
    padding:10px 20px;
    margin:10px 20px;
    border-radius: 12px;
    box-shadow: 0 0 4px 2px rgba(0,0,0,0.4);
    overflow: scroll;
    scrollbar-base-color: #c13eb8;
}
.recipeImage{
    border-radius: 50%;
    width:80px;
    height:80px;
    transition:0.3s ease;
}
.recipeImage:hover{
    width:120px;
    height:120px;
}
.loader,
.loader:before,
.loader:after {
  background: #c13eb8;
  -webkit-animation: load1 1s infinite ease-in-out;
  animation: load1 1s infinite ease-in-out;
  width: 1em;
  height: 4em;
}
.loader {
  color: #c13eb8;
  text-indent: -9999em;
  margin: 88px auto;
  position: relative;
  font-size: 11px;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.loader:before,
.loader:after {
  position: absolute;
  top: 0;
  content: '';
}
.loader:before {
  left: -1.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.loader:after {
  left: 1.5em;
}
@-webkit-keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}